<template>
	<div>
		<div class="user-profile">
			<div class="user-profile-avatar">
				<img src="../../../static/img/home/avatar.png">
			</div>
			<div class="user-profile-username">
				<span class="m-nick">{{userInfo.content.userName}}</span>
			</div>
		</div>

		<van-cell-group class="user-group">
			<van-cell title="我的订单"/>
			<van-row class="user-links">
				<router-link to="/orderList?type=0">
					<van-col span="6" style="color: black">
						<van-icon name="description"/>
						<div>未处理</div>
					</van-col>
				</router-link>
				<!--<router-link to="/orderList?type=2">-->
					<!--<van-col span="6" style="color: black">-->
						<!--<van-icon name="records"/>-->
						<!--<div>已处理</div>-->
					<!--</van-col>-->
				<!--</router-link>-->
				<router-link to="/orderList?type=3">
					<van-col span="6" style="color: black">
						<van-icon name="todo-list-o"/>
						<div>待接单</div>
					</van-col>
				</router-link>
				<router-link to="/orderList?type=4">
					<van-col span="6" style="color: black">
						<van-icon name="balance-list-o"/>
						<div>已接单</div>
					</van-col>
				</router-link>
				<router-link to="/orderList?type=1">
					<van-col span="6" style="color: black">
						<van-icon name="completed"/>
						<div>已完成</div>
					</van-col>
				</router-link>
			</van-row>
			<van-row class="user-links">
				<router-link to="/orderList?type=5">
					<van-col span="6" style="color: black">
						<van-icon name="failure"/>
						<div>已取消</div>
					</van-col>
				</router-link>
			</van-row>
		</van-cell-group>

		<van-cell-group class="user-group">
			<van-cell title="我的服务"/>
			<van-row class="user-links">
				<router-link to="/info">
					<van-col span="6" style="color: black">
						<van-icon name="user-circle-o"/>
						<div>账号管理</div>
					</van-col>
				</router-link>
				<router-link to="/addressList">
					<van-col span="6" style="color: black">
						<van-icon name="location-o"/>
						<div>收货地址</div>
					</van-col>
				</router-link>
				<router-link to="/serial">
					<van-col span="6" style="color: black">
						<van-icon name="search"/>
						<div>条码搜索</div>
					</van-col>
				</router-link>
			</van-row>
		</van-cell-group>

		<van-cell-group>
			<van-cell title="退出登录" is-link @click="signOutFunc"/>
		</van-cell-group>

		<navigate/>
	</div>
</template>

<script>
	import navigate from '../../components/footer/navigate.vue'
	import {mapState, mapActions} from 'vuex';

	export default {
		name: 'Home',

		components: {
			navigate
		},

		data() {
			return {}
		},

		computed: {
			...mapState(['userInfo']),
		},

		mounted() {
			this.$store.commit('SET_TABBAR_INDEX', 3)
		},

		methods: {
			...mapActions(['signOut']),

			signOutFunc() {
				this.signOut();
				this.$router.replace('/login');
			},
		},
	};
</script>

<style lang="less">
	.user {
		&-profile {
			text-align: center;
			display: block;
			width: 100%;
			height: 141px;
			background-color: #f1f5fa;
			background-repeat: no-repeat;
			background-size: 100% 100%;
			img {
				width: 100%;
			}
			&-avatar {
				padding-top: 23px;
				padding-bottom: 5px;

				img {
					width: 65px;
					height: 65px;
					border-radius: 50%;
					overflow: hidden;
				}
			}
			&-username {
				font-size: 20px;
			}
		}
		&-group {
			margin-bottom: .3rem;

			.van-cell__value {
				color: #999;
				font-size: 12px;
			}
		}
		&-links {
			padding: 15px 0;
			font-size: 12px;
			text-align: center;
			background-color: #fff;
			.van-icon {
				position: relative;
				width: 24px;
				font-size: 24px;
			}
		}
	}
</style>


